import MyPagination from "../../../components/MyPagination";
import mixins from "../../../utils/mixins";
import {common} from "../../../utils/api/url";
import {deviceType, MessageError, MessageSuccess, setDormBuildChildren} from "../../../utils/utils";
import LayoutTb from "../../../components/Layout/LayoutTb";
import MySelect from "../../../components/MySelect";
import MyUserType from "../../../components/utils/MyUserType";
import MyDatePicker from "../../../components/MyDatePicker";
import MyInputButton from "../../../components/search/MyInputButton";
import DialogNormal from "../../../components/utils/dialog/DialogNormal";
import DrawerLayoutRight from "../../../components/utils/dialog/DrawerLayoutRight";
import MyViewImg from "../../../components/utils/common/MyViewImg";
import screenManageValidater from "../../../utils/validater/screenManageValidater";
export default {
  mixins: [mixins, screenManageValidater],
  components: {MyPagination,LayoutTb,MySelect,MyUserType,MyDatePicker,MyInputButton,DialogNormal,DrawerLayoutRight,MyViewImg},
  data(){
    return {
      pageStudent: 1,
      numStudent: 20,
      totalStudent: 0,
      tableData: [],
      tableStudentData: [],
      searchDate: [],
      typeList: [],
      searchKey: '',
      visible: false,
      modalVisible: false,
      drawerVisible: false,
      dialogLoading: false,
      visibleConfim: false,
      imgVisibleConfim: false,
      showDialogName: false,
      showDialogYear: false,
      clearTime: '',
      action: '',
      subTitle: '',
      viewImage: '',
      dataSetOptions: [],
      dataYearOptions: [],
      dataProvinceOptions:[],
      dataProcessOptions: [],
      groupOptions: [],
      form: {
        id: '',
        groupName: ''
      },
      formModal: {
        id: '',
        name: '',
        templateId: '',
        dataSet: '',
        group: '',
        time: '',
        content: '',
        userUnit: '',
        year: '',
        schoolName: '',
        province: ''
      }
    }
  },
  created() {
    this.init();
    this.initStudent();
    this.initYear();
    this.initProvince();
    this.initProcess();
  },
  methods: {
    init(){
      let params = {
        page: this.page,
        num: this.num,
        beginTime: this.searchDate ? this.searchDate[0] : '',
        endTime: this.searchDate ? this.searchDate[1] : ''
      };
      params = this.$qs.stringify(params);
      this.$axios.post(common.stranger_page, params).then(res => {
        if (res.data.data){
          this.tableData = res.data.data.list;
          this.total = res.data.data.totalCount;
          this.num = res.data.data.num;
          this.page = res.data.data.currentPage;
        }
      });
    },
    initStudent(id){
      let params = {
        page: this.pageStudent,
        num: this.numStudent
      };
      let arr = [];
      this.$axios.get(common.screen_group_page, {params: params}).then(res => {
        if (res.data.data){
          this.tableStudentData = res.data.data.list;
          this.totalStudent = res.data.data.totalCount;
          this.numStudent = res.data.data.num;
          this.pageStudent = res.data.data.currentPage;

          arr.push({
            label: this.$t("默认组"),
            value: '',
          });
          for (let i = 0; i < res.data.data.list.length; i++){
            arr.push({
              label: res.data.data.list[i].group_name,
              value: res.data.data.list[i].id,
            });
          }
          this.groupOptions = arr;
        }
      });
    },
    initBuild(){
      let params = {
        buildingType: 1
      };
      let arr = [];
      this.$axios.get(common.hierarchical_build, {params: params}).then(res => {
        if (res.data.data){
          for (let i = 0; i < res.data.data.length; i++){
            arr.push({
              label: res.data.data[i].building_name,
              value: res.data.data[i].id,
            });
          }
          this.formModal.dataSet = arr[0].value;
          this.dataSetOptions = arr;
        }
      });
    },
    initYear(){
      let year = [];
      let params = {
        page: 1,
        num: 9999
      };
      this.$axios.get(common.enroll_page, {params: params}).then(res => {
        if (res.data.data){
          let year = [];
          for (let i = 0; i < res.data.data.list.length; i++){
            year.push({
              label: res.data.data.list[i].year,
              text: res.data.data.list[i].year,
              value: res.data.data.list[i].year,
            });
          }
          this.dataYearOptions = year;
        }
      });
    },
    initProvince(){
      let array = [];

      this.$axios.get('/json/province.json').then(res => {
        for (let i = 0; i < res.data.length; i++){
          array.push({
            label: res.data[i].label,
            value: res.data[i].label,
          });
        }

        this.dataProvinceOptions = array;
      });
    },
    async initProcess(){
      await this.getLinkProcessInfo();
      let data = this.dataProcessServer;
      let array = [];
      if (data && data.length > 0){
        array = data;
      }
      this.dataProcessOptions = array;
    },
    setInfo(){
      this.drawerVisible = true;
    },
    showView(viewImage){
      this.viewImage = viewImage;
      this.imgVisibleConfim = true;
    },
    addInfo(type){
      this.showDialogName = false;
      this.showDialogYear = false;
      if (type == 1){
        this.formModal.userUnit = 1;
        this.formModal.templateId = 7;
        this.dataSetOptions = [{
          label: this.$t("全校课堂数据"),
          value: ''
        }];
      }else if (type == 2){
        this.formModal.userUnit = 1;
        this.formModal.templateId = 5;
        this.dataSetOptions = [{
          label: this.$t("全校归寝数据"),
          value: ''
        }];
      }else if (type == 3){
        this.formModal.userUnit = 6;
        this.formModal.templateId = 6;
        this.initBuild();
      }else if (type == 4){
        this.formModal.userUnit = 6;
        this.formModal.templateId = 8;
        this.showDialogName = true;
        this.showDialogYear = true;
      }
      this.numStudent = 999;
      this.initStudent();
      this.modalVisible = true;
    },
    deleteInfo(row){
      let params = {
        id: row.id
      };
      params = this.$qs.stringify(params);
      this.$axios.post(common.screen_group_page_del, params).then(res => {
        if (res.data.code == 200){
          this.initStudent();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.dialogLoading = false;
      });
    },
    okDialog(event){
      let url = "";
      this.$refs['formModal'].validate((valid) => {
        if (valid) {
          this.dialogLoading = true;
          let params = {
            screenName: this.formModal.name,
            templateId: this.formModal.templateId,
            data: this.formModal.dataSet,
            duration: this.formModal.time,
            screenRemarks: this.formModal.content,
            userUnit: this.formModal.userUnit,
            comment: this.formModal.content,
            screenGroupId: this.formModal.group
          };
          if (this.formModal.templateId == 6){
            params['buildId'] = this.formModal.dataSet;
            params['data'] = this.formModal.dataSet;
          }
          if (this.formModal.templateId == 8){
            params['titleName'] = this.formModal.schoolName;
            params['enrollYear'] = this.formModal.year;
            params['enrollProvince'] = this.formModal.province;
            params['enrollProcessId'] = this.formModal.dataSet;
          }
          params = this.$qs.stringify(params);
          this.$axios.post(common.screen_add, params, {loading: false}).then(res => {
            if (res.data.code == 200){
              this.modalVisible = false;
              MessageSuccess(res.data.desc);
            }else {
              MessageError(res.data.desc);
            }
            this.dialogLoading = false;
          });
        }
      });
    },
    saveGroup(){
      let url = "";
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogLoading = true;
          let params = {
            groupName: this.form.groupName
          };
          url = common.screen_group_page_add;
          params = this.$qs.stringify(params);
          this.$axios.post(url, params).then(res => {
            if (res.data.code == 200){
              this.initStudent();
              MessageSuccess(res.data.desc);
            }else {
              MessageError(res.data.desc);
            }
            this.dialogLoading = false;
          });
        }
      });
    },
    search(data){
      this.searchKey = data.input;
      this.page = 1;
      this.init();
    },
    handleChange(data){

    },
    sizeChange(event){
      this.page = 1;
      this.num = event;
      this.init();
    },
    currentPage(event){
      this.page = event;
      this.init();
    },
    cancelDrawDialog(){
      this.drawerVisible = false;
    },
    closeDrawerDialog(event){
      this.form.groupName = "";
      this.form = {
        id: '',
        groupName: ''
      };
      if (this.$refs['form']){
        this.$refs['form'].resetFields();
      }
      this.drawerVisible = event;
    },
    sizeStudentChange(event){
      this.pageStudent = 1;
      this.numStudent = event;
      this.initStudent();
    },
    currentStudentPage(event){
      this.pageStudent = event;
      this.initStudent();
    },
    handleSelect(data, type){
      if (type == 1){
        this.formModal.dataSet = data;
      }else if (type == 2){
        this.formModal.group = data;
      }else if (type == 3){
        this.formModal.year = data;
      }else if (type == 4){
        this.formModal.province = data;
      }
    },
    closeDialog(event){
      this.form = {
        id: '',
        groupName: ''
      };
      this.formModal = {
        id: '',
        name: '',
        templateId: '',
        dataSet: '',
        group: '',
        time: '',
        content: '',
        userUnit: ''
      };
      this.numStudent = 20;
      this.dataSetOptions = [];
      if (this.$refs['formModal']){
        this.$refs['formModal'].resetFields();
      }
      if (this.$refs['form']){
        this.$refs['form'].resetFields();
      }
    },
    cancelDialog(){
      this.modalVisible = false;
    }
  }
}
